*, ::after, ::before {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  line-height: 1.5;
  background-image: url(../images/bg-pattern.609e53a8.png);
}

.form {
  background-color: #cfcfcf;
}

.form-item {
  text-align: center;
  box-sizing: border-box;
  padding: 30px;
  margin: 100px;
  position: static;
}

.form-label {
  display: block;
  color: #2b2a2a;
  font-size: 30px;
  line-height: 1.16;
  letter-spacing: 0.01em;
  margin-top: 10px;
}

.form-input {
  width: 400px;
  height: 40px;
  border: 1px solid #000000;
  border-radius: 30px;
  outline: none;

  padding: 20px;
  margin-top: 4px;
  margin-bottom: 10px;
}
